export default {
  name: "Guard",
  props: {
    allows: {
      type: Array,
      required: true
    },
    permissions: {
      type: Object,
      required: true
    }
  },
  watch: {
    isAllowed: {
      immediate: true,
      handler: function handler(isAllowed) {
        if (!isAllowed) {
          this.$emit('denied');
        }
      }
    }
  },
  computed: {
    isAllowed: function isAllowed() {
      var _this = this;

      return this.allows.filter(function (permission) {
        return _this.permissions[permission];
      }).length > 0;
    }
  }
};